.backdrop {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 20;
    background-color: rgba(0, 0, 0, 0.75);
}

.modal {
    position: fixed;
    top: 25vh;
    left: 5%;
    width: 90%;
    width: 771px;
    height: 568px;
    left: calc(50% - 385px);
    background-color: white;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
    z-index: 30;
    animation: slide-down 300ms ease-out forwards;
}

/* @media (min-width: 768px) {
    .modal {
        width: 771px;
        height: 568px;
        left: calc(50% - 385px);
    }
} */

@keyframes slide-down {
    from {
        opacity: 0;
        transform: translateY(-3rem);
    }

    to {
        opacity: 1;
        transform: translateY(0);
    }
}