.container {
    width: 771px;
    max-width: 771px;
    height: 568px;
}

.top {
    height: 55.796px;
    display: flex;
    flex-direction: row;
    align-items: center;
    padding-right: 21.8px;
    padding-left: 31px;
    background-color: #604EF8;
    justify-content: space-between;
    color: #FFF;
    font-size: 14px;
    font-family: 'Fira Sans';
    font-style: normal;
    font-weight: 500;
    line-height: normal;
}

.order-print {
    width: 100%;
    height: 511px;
}

.table {
    margin: 19px 31px 23px 35px;
    width: 704px;
    height: 469px;
    color: #555;
    text-align: center;
    font-size: 14px;
    font-family: 'Pretendard';
    font-style: normal;
    font-weight: 400;
    line-height: normal;
    border-top: 1px solid black;
    border-left: 1px solid black;
}

.blank {
    display: flex;
    height: 119px;
    border-bottom: 1px solid black;
}

.td-title {
    background: #F5F3FF;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 40%;
    height: 100%;
    color: #000;
    text-align: center;
    font-size: 14px;
    font-family: 'Inter';
    font-style: normal;
    font-weight: bold;
    line-height: normal;
    border-right: 1px solid black;
}

.td-content {
    display: flex;
    width: 100%;
    height: 100%;
    align-items: center;
    border-right: 1px solid black;
    padding: 0px 11px 0px 11px;
}

.blank:last-child {
    height: 229px;
}