.mainContainer {
  padding-top: 98px;
  padding-left: 232px;
  box-sizing: border-box;
  font-family: "Pretendard-Regular";
}

.todaySales {
  font-size: 32px;
  line-height: 48px;
  margin: 10px 0px;
  font-family: "Pretendard-Semibold";
}

.salesChartContainer {
  display: flex;
  width: 100%;
  height: 326px;
}

.salesChart {
  width: 593px;
  height: 289px;
  margin-top: 10px;
}

.salesBarChart {
  width: 593px;
  height: 349px;
  margin-top: 10px;
}

.percentage {
  width: 20%;
  height: 349px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  font-size: 18px;
  margin-top: 40px;
}

.percentageContainer {
    width: 100%;
  height: 60px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.percentageItem {
  display: flex;
  justify-content: space-around;
}

.percentageItemName {
    display: flex;
}

.percentageItemName div {
    margin-right: 8px;
}